.App{
  
  text-align: center;
  width: 317px;
  height:780px;
  overflow: hidden;
  border-radius: 10px 10px 0px 0px;
}


.info{
  border-radius: 10px 10px 0px 0px;
  background: #1A1B21;
  width: 100%;
}
.name{
  color: #fff;
  
  margin: 5px;
}
.occupation{
  color: #F3BF99;
  font-size: 12.8px;
  line-height: 15px;
  font-weight: normal;
  
  margin: 5px;
}

.site{
  
font-weight: normal;
font-size: 10.24px;
margin: 5px;


text-align: center;

color: #F5F5F5;
}

.btn{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
}

.email{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  width: 115px;
  height: 34px;

  background: #FFFFFF;
/* gray/300 */

border: 1px solid #D1D5DB;
box-sizing: border-box;
/* shadow/sm */

box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
border-radius: 6px;
}

.linkden{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  width: 115px;
  height: 34px;

  background: #FFFFFF;
/* gray/300 */

border: 1px solid #D1D5DB;
box-sizing: border-box;
/* shadow/sm */

box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
border-radius: 6px;
}

.email h3{
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: #374151;
}

.linkden{
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: white;
  background: #5093E2;
/* shadow/sm */
  border: none;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px
  
}

.linkden  ion-icon{
  padding-right: 10px;
  font-size: 1.5em;
}

.email ion-icon{
  padding-right: 10px;
  font-size: 1.5em;
  
} 


/* This is for the About section */
.about{
  background: #1A1B21;
  padding: 0 30px;
}
.about h3{
  margin: 0;
  text-align: start;
  padding-top: 5px;
  color: #F5F5F5;
}
.about .article{
  text-align: start;
  margin: 0;
  font-style: normal;
font-weight: normal;
font-size: 10.24px;
line-height: 150%;
/* or 15px */


color: #DCDCDC;
}


/* This is for the intrest section */
.intrest{
  background: #1A1B21;
  padding: 0 30px;
}

.intrest h3{
  margin: 0;
  text-align: start;
  padding-top: 10px;
  color: #F5F5F5;
}

.intrest .article{
  text-align: start;
  margin: 0;
  font-style: normal;
font-weight: normal;
font-size: 10.24px;
line-height: 150%;
/* or 15px */


color: #DCDCDC;
padding-bottom: 35px;
}

.icons{
  
  

  background: #161619;
  border-radius: 0px 0px 10px 10px;

  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 20px 40px 20px 40px;
}

.icons ion-icon{
  padding: 5px;
  color:black;
  
  background: #918E9B;
}